@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.cdnfonts.com/css/russo-one');
@import url('https://fonts.cdnfonts.com/css/inter');

body{
    background-color: #010139;
    background-image: url(./assets/catbg.svg);
    background-size: contain;
}

.inter{
    font-family: 'Inter', sans-serif;
                                                
}

.russo{
    font-family: 'Russo One', sans-serif;
}

.moving-object {  
    /* position: relative; */
    animation: moveUpDown 5s infinite alternate ease;
}
.moving-objectinverse {  
    /* position: relative; */
    animation: moveDownUp 5s infinite alternate ease;
}

@keyframes moveUpDown {
    0% {
        top: 0;
    }
    100% {
        top: 100px; /* Change this value to adjust the distance it moves */
    }
}

@keyframes moveDownUp {
    0% {
        top: 0;
    }
    100% {
        top: -100px; /* Change this value to adjust the distance it moves */
    }
}